:root{

  --white: #FFFFFF;
  
  --grey-900: #121416;
  --grey-800: #24282C;
  --grey-700: #373B40;
  --grey-600: #50555B;
  --grey-500: #696F76;
  --grey-400: #81878E;
  --grey-300: #9EA3A9;
  --grey-200: #B4B8BD;
  --grey-100: #C7CACE;
  --grey-50: #DCDEE1;
  --grey-10: #F2F3F5;
  

  --blue-900: #061B32;
  --blue-800: #0D2845;
  --blue-700: #17406E;
  --blue-600: #214973;
  --blue-500: #315D8B;
  --blue-400: #4676A8;
  --blue-300: #648DBA;
  --blue-200: #85A7CC;
  --blue-100: #A9C2DD;
  --blue-50: #CCDBEB;
  --blue-10: #F1F5F9;

  }
